// 组织树数据
export const getTreeData = (params) =>
  defHttp.post(
    {
      url: Api.TreeData,
      params,
    },
    {},
  );

// 列表数据
export const getDataList = (params) =>
  defHttp.post(
    {
      url: Api.DataList,
      params,
    },
    {},
  );

// 详细数据
export const getDetailList = (params) =>
  defHttp.get(
    {
      url: Api.DetailList,
      params,
    },
    {},
  );

// 新增
export const insert = (params) =>
  defHttp.post(
    {
      url: Api.InsertUrl,
      params,
    },
    {},
  );

// 查询新增进度
export const perQuery = (params) =>
  defHttp.post(
    {
      url: Api.perQuery,
      params,
    },
    {},
  );
// 导出
export function exportSunriseSunset(params) {
  return defHttp.download({ url: Api.exportExcelUrl, params }, '日出数据.xls');
}
